.cart-item-container{
    
    width: 80%  ;
    border-radius: 10px;
    margin: 10px auto;
    display: flex;
    flex-direction: row;
    justify-content: center;
}
.cart-img-container{
    display: flex;
    width: 30%;
    justify-content: center;
    align-items: center;
}
.cart-img-container>img{
    width: 80px;
    height: auto;
}
.cart-info-container{
    width: 100%;
}